import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
const njs = require('navcoin-js')

njs.wallet.Init().then(async () => {
    ReactDOM.render(
        <React.StrictMode>
            <App njs={njs}/>
        </React.StrictMode>,
        document.getElementById('root')
    );
});
